<template>
    <div class="test">
        <!-- 获取数据 -->
        <h1 style="">{{title}}</h1>
        <p>{{user.fristName}}</p>
        <p v-text="user.lastName"></p>
        <!-- v-if -->
        <p v-if="showName">{{user.fristName}}</p>
        <!-- v-else -->
        <p v-else>Nobody</p>
        <ul>
            <li v-for="item in items" v-bind:key="item.id">{{item.title}}</li>
        </ul>
        <!-- v-on -->
        <button v-on:click="greet('Hellow Everybody!')">Say Greeting</button>
        <!-- 键盘事件 -->
        <input type="text" v-on:keyup="pressKey" v-on:keyup.enter="enterHit">
        <hr>
        <!-- conputed 计算属性 -->
        <label>First Name</label>
        <input type="text" v-model="user.fristName">
        <br>
        <label>Last Name</label>
        <input type="text" v-model="user.lastName">
        <br>
        <h3>{{fullName}}</h3>
        <!-- props 属性 -->
        <h2>{{msg}}</h2>

    </div>
</template>
<script>
export default {
    name:"test",
    props:{
        msg:{
            type:String,
            default:"默认就是当前这些文字"
            
        }
    },
    data(){
        return{
            title:"中本硕员工状态栏",
            user:{
                fristName:"yuntao",
                lastName:"yuan"
            },
            showName:true,
            items:[
                {title:"item 1"},
                {title:"item 2"},
                {title:"item 3"}
            ]
            
        }
    },
    methods:{
         greet:function(greeting){
             alert(greeting);
         },
          pressKey:function(){
             console.log("pressed.....");
         },
         enterHit:function(){
             console.log("you pressed enterKey.....");
         }
         
    },
    computed:{
         fullName:function(){
            return this.user.fristName + " " + this.user.lastName
         },
    }

}
</script>
<style scoped>


</style>



