
<template>

  <div class="zbs" >
    <div class="tit" >
    <h1>{{title}}</h1>
    <h5>{{dateFormat(date)}}</h5>
    </div>

   <div class="PLC">
      <table border="10px" style="width:30%;height:100%">
        <th>
          <label>PLC部</label>
        </th>
      </table>
     

      <table border="10px" style="width:100%;height:100%">
        <tr class="mytest">
          <th v-for="item in datas.columnList" v-bind:key="item.id" width="30%">{{item.dataName}}</th>
        </tr>
        <tr v-for="item in datas.inforList" v-bind:key="item.id">
          <td v-for="it in datas.columnList" v-bind:key="it.id">{{item[it.dataItem]}}</td>
        </tr>
      </table>
    
</div>
<div class="Business">
      <table border="1px" width="100%" margin-left=40% >
        <th>
          <label>事业部</label>
        </th>
      </table>

      <table border="1px" width="100%" height="60%">
        <tr class="mytest">
          <th v-for="item in datas.columnList" v-bind:key="item.id" width="30%">{{item.dataName}}</th>
        </tr>
        <tr v-for="item in datas.inforList" v-bind:key="item.id">
          <td v-for="it in datas.columnList" v-bind:key="it.id">{{item[it.dataItem]}}</td>
        </tr>
      </table>
    </div>
</div>
      

</template>




<script>
export default {
  name: "zbs",

  data() {
    return {
      title: "中本硕员工状态栏",
      //自定义循环数据
      nowTime: "",
      date: new Date(),
      datas: {
        columnList: [
          {
            dataItem: "name",
            dataName: "姓名"
          },
          {
            dataItem: "address",
            dataName: "地址"
          },
          {
            dataItem: "statu",
            dataName: "状态"
          }
        ], //字段
        inforList: [
          {
            name: "元云涛",
            address: "公司",
            statu: "上班"
          },

          {
            name: "王应龙",
            address: "公司",
            statu: "装逼"
          },
        
        ]
      }
    };
  },
  methods: {
    greet: function(greeting) {
      alert(greeting);
    },
    pressKey: function() {
      console.log("pressed.....");
    },
    enterHit: function() {
      console.log("you pressed enterKey.....");
    },
    //时间格式化函数，此处仅针对yyyy-MM-dd hh:mm:ss 的格式进行格式化
    dateFormat(time) {
      var date = new Date(time);
      var year = date.getFullYear();
      /* 在日期格式中，月份是从0开始的，因此要加0
       * 使用三元表达式在小于10的前面加0，以达到格式统一  如 09:11:05
       * */
      var month =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      var day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      var hours =
        date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
      var minutes =
        date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
      var seconds =
        date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
      var week = "星期" + "日一二三四五六".charAt(date.getDay());
      // 拼接
      return (
        year +
        "-" +
        month +
        "-" +
        day +
        " " +
        "  " +
        week +
        "   " +
        hours +
        ":" +
        minutes +
        ":" +
        seconds
      );
    }
  },

  mounted() {
    var _this = this; //声明一个变量指向vue实例this,保证作用域一致
    this.timer = setInterval(function() {
      _this.date = new Date(); //修改数据date
    }, 1000);
  },
  beforeDestroy() {
    if (this.timer) {
      clearInterval(this.timer); //在vue实例销毁钱，清除我们的定时器
    }
  }
};
</script>

<style>
 #zbs {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #503f2c;
  margin-top: 10px;
  width:100%;
  border:1px solid #000000;
 
} 
#tit {
  width: 100%;
  border: 1px solid #000000;
  
}
#dht {
  padding: 10px;
  border: 1px solid#000000;
  background-color: #a2a2ff;
}
#PLC {
  float: left;
  width: 60%;
  height: 600px;
  border: 1px solid #000000;
  text-align: center;
  margin-right: 10px;
  margin-left: 10px;
}
#Business {
  float: right;
  margin-right: 10px;
  width: 35%;
  border: 1px #a2a2ff;
  height: 600px;
}
#jz {
  padding: 10px;
  border: 3px #e8ee97;
  text-align: center;
  clear: both;
  margin-top: 20px;
}
</style>


